@import url(https://fonts.googleapis.com/css2?family=Nunito:wght@400&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  margin: 0;
  padding: 0;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
  box-sizing: border-box;
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}


* {
  transition: all 0.02s;
}
.Phone_Container {
  display: flex;
  flex-direction: column;
  height: 640px;
  width: 360px;
  /* border: 10px solid black; */
  border-radius: 35px;
  padding: 20px 10px;
  box-shadow: 0px 0px 15px rgba(0, 255, 242, 0.568);
  -webkit-animation-name: blinking;
          animation-name: blinking;
  -webkit-animation-duration: 2s;
          animation-duration: 2s;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
  -webkit-animation-direction: alternate;
          animation-direction: alternate;
}
@media (max-width: 400px) {
  .Phone_Container {
    height: 80vh;
    width: 80vw;
    border: transparent;
  }
}

@-webkit-keyframes blinking {
  0% {
    box-shadow: 0px 0px 15px rgba(0, 255, 242, 0);
  }
  25% {
    box-shadow: 0px 0px 15px rgba(0, 255, 242, 0.25);
  }
  50% {
    box-shadow: 0px 0px 15px rgba(0, 255, 242, 0.5);
  }
  100% {
    box-shadow: 0px 0px 15px rgba(0, 255, 242, 1);
  }
}

@keyframes blinking {
  0% {
    box-shadow: 0px 0px 15px rgba(0, 255, 242, 0);
  }
  25% {
    box-shadow: 0px 0px 15px rgba(0, 255, 242, 0.25);
  }
  50% {
    box-shadow: 0px 0px 15px rgba(0, 255, 242, 0.5);
  }
  100% {
    box-shadow: 0px 0px 15px rgba(0, 255, 242, 1);
  }
}

.Image_Container {
  display: flex;
  justify-content: center;
  align-items: center;
}
.Image_Container img {
  width: 100px;
  border-radius: 50%;
  box-shadow: 0 0 10px 5px rgba(255, 255, 255, 0.2);
}

.Heading_Container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  font-size: large;
  padding: 15px 0px;
}
.Heading_Container .Name {
  font-weight: 900;
  font-size: larger;
  letter-spacing: 0.5px;
  font-family: "Nunito", sans-serif;
}
.Heading_Container .Greeting {
  font-weight: 500;
  font-size: large;
  letter-spacing: 1px;
}

.Links_Container {
  display: flex;
  flex-direction: column;
  padding: 0px 30px;
}
.Links_Container a {
  text-decoration: none;
  color: inherit;
}
.Links_Container .Link_Container {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 10px;
  /* border: 2px solid white; */
  box-shadow: 0px 0px 10px rgba(245, 245, 245, 0.342);
  border-radius: 6px;
  cursor: pointer;
}
.Links_Container .Link_Container:hover {
  background-color: #282c34d8;
  /* border: 2px solid rgb(0, 128, 32); */
  border: none;
  box-shadow: 0px 0px 15px rgba(240, 225, 88, 0.575);
}
.Links_Container .Link_Container .Link {
  font-weight: 600;
  font-family: "Nunito", sans-serif;
  letter-spacing: 2px;
}

.Social_Container {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 20px 0px;
}
.Social_Container .Social_Link {
  cursor: pointer;
  font-size: larger;
  padding: 5px;
}
.Social_Container .Social_Link a {
  text-decoration: none;
  color: inherit;
}

